  transition: all .3s ease-in-out;
}

.header__buttons-toggle:after, .header__buttons-toggle:before {
  content: "";
  background-color: #292929;
  border-radius: 3.125rem;
  width: 1.5rem;
  height: .1875rem;
  transition: all .3s ease-in-out;
