}

.header__buttons-toggle:hover:before {
  width: 70%;
}

.header__buttons-toggle--active small {
  transform: scale(0);
}

.header__buttons-toggle--active:after {
  top: .375rem;
  transform: rotate(45deg);
}

.header__buttons-toggle--active:before {
  bottom: .4375rem;
